@import 'colors.scss';

.container {
  width: calc(100% - 20px);
  height: calc(100% - 20px);
  margin: 10px;
}

.tabs {
  width: 43.7%;
  height: calc(100% - 1px);

  border-bottom: 1px solid $grey;
}

.bottomContainer {
  display: flex;
  height: 100%;
  width: 100%;
}

.tabContainer {
  position: relative;
  display: flex;
  flex-grow: 1;

  margin-right: 10px;
}